:root{
    --primary-color: #e0c521;
}

.card{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 150px;
    height: 240px;
    border: 2px solid var(--primary-color);
    padding: .5rem .3rem;
    margin: 12px 10px;
    position: relative;
    border-radius: 10px;
}

.card__title{
    font-weight: 400;
    text-align: center;

}

.card__title .card__price{
    font-weight: bold;
}
.image__container{
    width: 100px;
    height: 100px;
    margin: 0 auto;
}
.image__container img{
    width: 100%;
    object-fit: cover;
}

.btn__container{
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.card__price{
    text-align: center;
    display: inline-block;
}

.card__badge{
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    background-color: rgb(227, 112, 30);
    border-radius: 50px;
    transform: translate(20px, -20px) scale(1);
    color: white;
    font-weight: bold;
    text-align: center;
    border: 2px solid  rgb(227, 112, 30);
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s ease;
    animation: scaleamnimation 1s ease infinite;
    opacity: 0.8;
}
@keyframes scaleamnimation {
    to{
        transform: translate(20px, -20px) scale(1.05);
        opacity: 1;

    }
}
.card__badge--hidden{
    display: none;
}